import React, { useState } from "react";
import "./App.css";

var INTERVAL;
const MINUTE = 60000;

const todos = ["WORK", "SIDE PROJECT", "READING", "OTHER"];

const App = () => {
  const [display, setDisplay] = useState("Focus Time!");
  const [step, setStep] = useState("stopped");
  const [task, setTask] = useState("");

  const stop = () => {
    setStep("stopped");
    if (INTERVAL) clearInterval(INTERVAL);
    setDisplay("Focus Time!");
  };

  const play = (target, callback) => {
    if (INTERVAL) clearInterval(INTERVAL);
    const start = new Date();
    INTERVAL = setInterval(() => update(start, target, callback), 42);
  };

  const work = t => {
    setStep("working");
    setTask(t);
    play(25, () => {
      console.log("end");
      setStep("tracking");
      setDisplay("Work Session Completed!");
    });
  };

  const takeBreak = x => {
    setStep("relaxed");
    play(x, () => {
      setStep("stopped");
      setDisplay("Focus Time!");
    });
  };

  const update = (start, target, callback) => {
    const time = target * MINUTE - (new Date() - start);
    const minutes = Math.floor(time / 60000);
    const seconds = Math.floor(time / 1000) % 60;
    const centi = Math.floor(time / 10) % 100;
    const d = x => (x < 10 ? "0" + x : "" + x);
    setDisplay(d(minutes) + ":" + d(seconds) + ":" + d(centi));
    if (time < 0) {
      stop();
      document.getElementById("audio").play();
      callback();
    }
  };

  return (
    <div className="App">
      <audio id="audio">
        <source src="powerUp6.mp3" type="audio/mpeg" />
      </audio>
      <div className="timer">{display}</div>
      {(step === "relaxed" || step === "working") && (
        <div className="button" onClick={stop}>
          STOP
        </div>
      )}
      {step === "stopped" && (
        <div
          className="button"
          onClick={() =>
            setStep("todo") || setDisplay("Choose what to focus on.")
          }
        >
          START WORKING
        </div>
      )}
      {step === "working" && <div className="button">FOCUS ON {task}</div>}
      {step === "todo" &&
        todos.map(t => (
          <div className="button" onClick={() => work(t)}>
            {t}
          </div>
        ))}
      {step === "tracking" && (
        <div className="button" onClick={() => setStep("pause")}>
          SUCCESS
        </div>
      )}
      {step === "tracking" && (
        <div className="button" onClick={() => setStep("pause")}>
          FAILURE
        </div>
      )}
      {step === "pause" && (
        <div className="button" onClick={() => takeBreak(5)}>
          5min BREAK
        </div>
      )}
      {step === "pause" && (
        <div className="button" onClick={() => takeBreak(15)}>
          15min BREAK
        </div>
      )}
      {step === "relaxed" && <div className="button">Enjoying Life</div>}
    </div>
  );
};

export default App;
